<template>
  <div class="bookshelf">
    <div class="setUp">
      <div class="setLeft">
        <div class="tabs">
          <a :class="{selected:active === 'tab-container1'}" @click="active = 'tab-container1'">作品</a>
          <a :class="{selected:active === 'tab-container2'}" @click="active = 'tab-container2'">关注</a>
        </div>
      </div>
      <div class="setRight">
        <i class="mintui mintui-search" @click="goSearch"></i>
      </div>
    </div>
    <div class="page-tab-container">
      <mt-tab-container class="page-tabbar-tab-container" v-model="active" swipeable>
        <mt-tab-container-item id="tab-container1">
          <div>
            <div class="content">
              <div class="books" v-for="(b,index) in bookList" @click="details(b._id)">
                <div class="bLeft">
                   <img :src="staUrl + b.cover" class="sImg">
                </div>
                <div class="sug tf">
                  <div class="sugge">{{b.title}}</div>
                  <div class="sugge"><span>{{b.author}}</span> 著 | {{b.minorCate}}</div>
                  <div class="sugge">{{b.lastChapter}}
                    <a><i class="mintui mintui-gengduo" @click="more(b)"></i></a>
                  </div>
                </div>
              </div>
            </div>
            <div class="brower">
                <button>浏览记录</button>
            </div>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="tab-container2">
           11
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
    <mt-popup
      v-model="popupVisible"
      position="bottom" class="popup">
      <div>
        <div class="d1">
          <img :src="book.path">
          <span>{{book.name}}</span>
          <span class="sp2">详情</span>
        </div>
        <div class="d2">
          <div class="img">
             <img src="../assets/2.jpg">
          </div>
          <div class="fans">
            <div>今天去拜拜</div>
            <div>当前粉丝值0<span></span> 未上榜</div>
            <div>
              再获得500粉丝值升级为"学徒"
            </div>
          </div>
        </div>
        <div class="swi">
          <div>自动订阅最新章节</div>
          <div>
            <mt-switch :value.sync="value"></mt-switch>
          </div>
        </div>
        <div class="operate">
          <div class="ope">
            <i class="mintui mintui-xiazai"></i>
            <div>下载</div>
          </div>
          <div class="ope">
            <i class="mintui mintui-yidong"></i>
            <div>移动</div>
          </div>
          <div class="ope">
            <i class="mintui mintui-shanchu"></i>
            <div>删除</div>
          </div>
          <div class="ope">
            <i class="mintui mintui-zhiding"></i>
            <div>置顶</div>
          </div>
          <div class="ope">
            <i class="mintui mintui-fenxiang"></i>
            <div>分享</div>
          </div>
          <div class="ope">
            <i class="mintui mintui-yuepiao cr"></i>
            <div>月票</div>
          </div>
          <div class="ope">
            <i class="mintui mintui-tuijian cr"></i>
            <div>推荐</div>
          </div>
          <div class="ope">
            <i class="mintui mintui-dashang cr"></i>
            <div>打赏</div>
          </div>
          <div class="ope">
            <i class="mintui mintui-pinglun cr"></i>
            <div>书评</div>
          </div>
          <div class="ope">
            <i class="mintui mintui-hongbao cr"></i>
            <div>红包</div>
          </div>
        </div>
      </div>
    </mt-popup>
  </div>
</template>
<script>
import {Indicator} from 'mint-ui';
import {mapActions} from 'vuex';
import * as config from '../config/config.js';
export default{
  name:'bookshelf',
  data(){
    return {
      bookList:[],
      popupVisible: false,
      active:'tab-container1',
      book:{},
      value:false,
      staUrl:'http://statics.zhuishushenqi.com',
    }
  },
  created(){
    // Indicator.open('加载中');
    // this.$axios.post('/vueBook/bookList',{})
    // .then((res)=>{
    //   if(res.data.state === 200){
    //     Indicator.close();
    //     this.bookList = res.data.data;
    //   }
    // })
    // .catch((err)=>{
    //   console.log(err)
    // });
    var url =   '/api/book/accurate-search?author=忘语';
    this.axios.get(url)
      .then((res)=> {
        this.bookList = res.data.books;
      })
  },
  methods:{
    ...mapActions([
      'setBidAct'
    ]),
    more(obj){
      this.book = obj;
      this.popupVisible = true;
    },
    details(id){
      this.axios.get('http://novel.juhe.im/book-sources?view=summary&book='+id)
      .then((res)=>{
        this.setBidAct(res.data.data[0]._id);
        this.id = res.data.data[0]._id;
      });
      this.$router.push({
        name:'bookDetails',
        params:{id:id}
      })
    },
    goSearch(){
      this.$router.push('searchBook')
    }
  }
}
</script>
<style scoped>
.bookshelf{
  margin-bottom: 3rem;
  overflow: hidden;
}
  .content {
    margin-bottom: 1rem;
  }
  .content .books:last-child {
    border-bottom: 1px solid #ddd;
  }


  .brower {
    margin-bottom:1rem;
  }
  .brower button {
    padding: 0.5rem 1rem;
    border: 1px solid red;
    color: red;
    border-radius: 30px;
    background: #fff;
  }
  .actived {
    color:red;
  }
  .popup {
    width:100%;
    font-family: 宋体;
  }
  .d1 {
    line-height: 3rem;
    height: 3rem;
    background-image: url(../../static/bc.png);
    color:#EE7700;
  }
  .d1 span{
    float: left;
    padding-left: 1rem;
  }
  .d1 .sp2 {
    float: right;
    padding-right: 1rem;
  }
  .d1 img {
    width: 3rem;
    height: 3.5rem;
    float: left;
    margin-top: -0.5rem;
    padding-left: 0.5rem;
  }
  .d2 {
    overflow: hidden;
    clear:both;
    padding: 0.5rem;
    border-bottom: 1px solid #ddd;
  }
  .d2 .img {
    float: left;
    margin: 0.75rem 0.25rem;
    height: 3rem;
  }
  .d2 img {
    height: 3rem;
    width: 3rem;
    border-radius: 24px;
  }
  .fans {
    float: left;
    text-align: left;
    font-size: 14px;
    padding-left: 0.5rem;
    letter-spacing: 1px;
  }
  .fans div {
    color:#777;
    padding:0.25rem 0;
  }
  .fans div:first-child {
    color:#000;
    font-weight: 600;
  }
  .fans div:last-child {
    color:#0055ff;
  }
  .swi:after {
    clear: both;
    content: '';
    display: block;
  }
  .swi div:first-child {
    float:left;
    padding-left:1rem;
    line-height: 2rem;
  }
  .swi div:last-child {
    float:right;
    padding-right:1rem;
  }

  .operate {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  .operate .ope {
    width: 3.8rem;
    padding: 0.5rem 0;
  }
  .ope i {
    font-size: 20px;
  }
  .ope div {
    font-size:14px;
    margin-top: 0.3rem;
  }
  .cr {
    color:red;
  }
</style>


